import React from 'react';
import Helmet from 'react-helmet';
import { injectIntl } from 'gatsby-plugin-intl';
import { useStaticQuery, graphql } from 'gatsby';

import leaf from '../assets/icons/leaf.png';

const SEO = injectIntl(({ intl, title }) => {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            siteUrl
          }
        }
      }
    `,
  );

  return (
    <Helmet
      htmlAttributes={{ lang: `${intl.formatMessage({ id: 'lang' })}` }}
      title={title}
      titleTemplate={intl.formatMessage({ id: 'title' })}
      meta={[
        {
          property: `title`,
          content: `${intl.formatMessage({ id: 'title' })}`,
        },
        {
          property: `og:title`,
          content: `${intl.formatMessage({ id: 'title' })}`,
        },
        {
          name: `twitter:title`,
          content: `${intl.formatMessage({ id: 'title' })}`,
        },
        {
          name: `description`,
          content: `${intl.formatMessage({ id: 'description' })}`,
        },
        {
          property: `og:description`,
          content: `${intl.formatMessage({ id: 'description' })}`,
        },
        {
          name: `twitter:description`,
          content: `${intl.formatMessage({ id: 'description' })}`,
        },
        {
          property: `og:url`,
          content: `${site.siteMetadata.siteUrl}`,
        },
        {
          property: `og:type`,
          content: `article`,
        },
        {
          name: `twitter:card`,
          content: `summary`,
        },
        {
          property: `og:image`,
          content: `${site.siteMetadata.siteUrl}/share/share.jpg`,
        },
        {
          property: `og:image:type`,
          content: `image/jpeg`,
        },
        {
          property: `og:image:width`,
          content: `1200`,
        },
        {
          property: `og:image:height`,
          content: `675`,
        },
      ]}
      link={[{ rel: 'icon', type: 'image/png', href: `${leaf}` }]}
    />
  );
});

export default SEO;
