import React from 'react';
import { injectIntl } from 'gatsby-plugin-intl';

import pizza from '../assets/icons/pizza.png';
import arrow from '../assets/icons/arrow-right-2.png';

import styles from './Infographic.module.scss';

const Hero = injectIntl(({ intl }) => {
  return (
    <section className={`${styles.info}`}>
      {/* Infographic */}
      <div className={`container container--grid`}>
        <div
          className={styles.lead}
          dangerouslySetInnerHTML={{
            __html: intl.formatHTMLMessage({ id: 'info_lead' }),
          }}
        />
        <div className={styles.chartContainer}>
          <p>{intl.formatMessage({ id: 'info_chart' })}</p>
          <img src={pizza} />
        </div>
        <div className={styles.score}>
          <p>
            <span className={styles.scoreNum}>
              {intl.formatMessage({ id: 'info_score_number' })}
            </span>
            <span className={styles.scoreText}>
              {intl.formatMessage({ id: 'info_score_text' })}
            </span>
            <span className={styles.scoreDetails}>
              {intl.formatMessage({ id: 'info_score_details' })}
            </span>
          </p>
        </div>
      </div>

      {/* Reminder */}
      <div className={`container container--grid ${styles.reminder}`}>
        <div
          className={styles.reminderTitle}
          dangerouslySetInnerHTML={{
            __html: intl.formatHTMLMessage({ id: 'reminder_title' }),
          }}
        />
        <div className={styles.reminderText}>
          <p>{intl.formatMessage({ id: 'reminder_text' })}</p>
        </div>
        <div className={styles.reminderArrow}>
          <img src={arrow} />
        </div>
        <div className={styles.reminderCTA}>
          <a href="https://www.google.com" target="_blank">
            {intl.formatMessage({ id: 'reminder_cta' })}
          </a>
        </div>
      </div>
    </section>
  );
});

export default Hero;
