import React from 'react';
import { injectIntl } from 'gatsby-plugin-intl';
import ReactPlayer from 'react-player';
import { Waypoint } from 'react-waypoint';

import styles from './VideoPreview.module.scss';

const VideoPreview = injectIntl(({ intl }) => {
  let player;

  const onEnter = () => {
    if (player) player.playVideo();
  };

  const onLeave = () => {
    if (player) player.pauseVideo();
  };

  return (
    <Waypoint
      onEnter={onEnter}
      onLeave={onLeave}
      bottomOffset="20%"
      topOffset="20%"
    >
      <section className={styles.videoPreview} id="video">
        <div className={`container`}>
          <h2 className={styles.title}>
            {intl.formatMessage({ id: 'hero_button_text' })}
          </h2>
          <div className={styles.playerContainer}>
            <ReactPlayer
              className={styles.player}
              playsinline
              width="100%"
              height="100%"
              url="https://youtu.be/FPw2qN21msw"
              config={{
                youtube: {
                  playerVars: {
                    showinfo: 0,
                    controls: 1,
                    hl: intl.locale,
                    cc_lang_pref: intl.locale,
                    cc: 1,
                    cc_load_policy: 1,
                  },
                },
              }}
              onReady={(e) => {
                player = e.player.player.player;
              }}
            />
          </div>
        </div>
      </section>
    </Waypoint>
  );
});

export default VideoPreview;
